import _ from 'lodash'

import Table from '../../mixins/table'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Table
  ],

  data() {
    return {
      action: null,
      player: {
        account_no: null,
        username: '',
        amount: null
      },
      route,
    }
  },

  computed: {
    data() {
      return [
        {
          account_no: 'LK000001',
          username: 'PL1',
          running_total: 100
        }
      ]
    },

    dialogTitle() {
      return 'Load player'
    }
  },

  methods: {
    handleClose(done) {
      this.action = null
      this.player = {
        account_no: null,
        username: '',
        amount: null
      }

      if (typeof done == 'function') {
        done()
      }
    },

    openDialog(action, player) {
      if (player) {
        this.player = player
      }

      this.action = action
      this.showDialog = true
    },

    closeDialog() {
      this.showDialog = false
      this.handleClose()
    },

    async save() {

    }
  }
}



